import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardTitle,
  CardBody,
  CardText,
} from "reactstrap";
import { Loading } from "./LoadingComponent";

function RenderSalary({ staff }) {
  if (staff != null) {
    return (
      <div>
        <div>
          <Card>
            <CardTitle className="ml-1">{staff.name}</CardTitle>
            <CardBody>
              <CardText>M nhn vin: {staff.id}</CardText>
              <CardText>H s lng: {staff.salaryScale}</CardText>
              <CardText>S ngy lm thm: {staff.overTime}</CardText>
            </CardBody>
            <p className="bg-secondary text-white ml-3 mr-3">
              Lng: {staff.salary}
            </p>
          </Card>
        </div>
      </div>
    );
  } else {
    return <div></div>;
  }
}

class Salary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sortArray: [],
    };
    this.sortByAsc = this.sortByAsc.bind();
    this.sortByDesc = this.sortByDesc.bind();
  }

  sortByAsc = () => {
    let currentList = this.props.staffsSalary.staffsSalary;
    let newList = currentList.sort((a, b) => a.salaryScale - b.salaryScale);
    this.setState({
      sortArray: newList,
    });
  };

  sortByDesc = () => {
    let currentList = this.props.staffsSalary.staffsSalary;
    let newList = currentList.sort((a, b) => b.salaryScale - a.salaryScale);
    this.setState({
      sortArray: newList,
    });
  };

  render() {
    const bangLuong = this.state.sortArray.map((staff) => {
      return (
        <div key={staff.id} className="col-12 col-sm-6 col-md-4 mt-4">
          <RenderSalary staff={staff} />
        </div>
      );
    });
    if (this.props.salaryLoading) {
      return (
        <div className="container">
          <div className="row">
            <Loading />
          </div>
        </div>
      );
    } else if (this.props.salaryErrMess) {
      return (
        <div className="container">
          <div className="row">
            <h4>{this.props.salaryErrMess}</h4>
          </div>
        </div>
      );
    } else
      return (
        <div className="container">
          <div className="row">
            <Breadcrumb>
              <BreadcrumbItem>
                <Link to="/nhanvien">Nhn Vin</Link>
              </BreadcrumbItem>
              <BreadcrumbItem active>Bng Lng</BreadcrumbItem>
            </Breadcrumb>
          </div>
          <button className="btn btn-success mr-3" onClick={this.sortByAsc}>
            Sp xp theo h s lng tng dn
          </button>
          <button className="btn btn-danger" onClick={this.sortByDesc}>
            Sp xp theo h s lng gim dn
          </button>
          <div className="row">{bangLuong}</div>
        </div>
      );
  }

  componentDidMount() {
    this.setState({
      sortArray: this.props.staffsSalary.staffsSalary,
    });
  }
}

export default Salary;



