import React, { Component } from "react";
import {
  Button,
  Card,
  CardImg,
  Form,
  FormGroup,
  Input,
  Col,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  Row,
} from "reactstrap";
import { Link } from "react-router-dom";
import { Control, LocalForm, Errors } from "react-redux-form";
import { Loading } from "./LoadingComponent";

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const isNumber = (val) => !isNaN(Number(val));

// Hin th danh sch nhn vin
function RenderMenuStaffs({ staff, removeStaff }) {
  return (
    <Card>
      <Link to={`/nhanvien/${staff.id}`}>
        <CardImg width="100%" src={staff.image} alt="" />
        <p className="text-center text-dark">{staff.name}</p>
      </Link>
      <Button
        onClick={() => removeStaff(staff.id)}
        type="submit"
        color="warning"
      >
        {" "}
        <span className="fa fa-trash"></span> Delete
      </Button>
    </Card>
  );
}

class StaffList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filterArray: [],
      isModalOpen: false,
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  //Hm tm kim nhn vin
  handleSearch = (event) => {
    event.preventDefault();
    let currentList = [];
    let newList = [];
    if (this.search.value !== "") {
      currentList = this.props.staffs.staffs;
      newList = currentList.filter((item) => {
        return item.name
          .toLowerCase()
          .includes(this.search.value.toLowerCase());
      });
    } else {
      newList = this.props.staffs.staffs;
    }
    this.setState({
      filterArray: newList,
    });
  };

  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen,
    });
  }

  //hm thm nhn vin mi
  handleSubmit(values) {
    this.props.postStaff(
      values.name,
      values.doB,
      values.startDate,
      values.department,
      values.salaryScale,
      values.annualLeave,
      values.overTime
    );
    this.toggleModal();
  }

  render() {
    const danhsachnv = this.state.filterArray.map((staff) => {
      return (
        <div key={staff.id} className="col-6 col-sm-4 col-md-2 mt-4">
          <RenderMenuStaffs
            staff={staff}
            removeStaff={this.props.removeStaff}
          />
        </div>
      );
    });

    //Kiem tra trang thai state, su dung component Loading
    if (this.props.staffLoading) {
      return (
        <div className="container">
          <div className="row">
            <Loading />
          </div>
        </div>
      );
    } else if (this.props.staffErrMess) {
      return (
        <div className="container">
          <div className="row">
            <h4>{this.props.staffErrMess}</h4>
          </div>
        </div>
      );
    } else
      return (
        <div className="container">
          <div className="row">
            <div className="col-12 col-md-6 mt-3">
              <div className="row">
                <div className="col-8 col-md-8">
                  <h4>Danh Sch Nhn Vin</h4>
                </div>
                <div className="col-4 col-md-4">
                  <Button
                    onClick={this.toggleModal}
                    type="submit"
                    color="primary"
                  >
                    <span className="fa fa-plus"></span>
                  </Button>
                  <Modal
                    isOpen={this.state.isModalOpen}
                    toggle={this.toggleModal}
                  >
                    <ModalHeader toggle={this.toggleModal}>
                      Thm Nhn Vin
                    </ModalHeader>
                    <ModalBody>
                      <LocalForm
                        onSubmit={(values) => this.handleSubmit(values)}
                      >
                        <Row className="form-group">
                          <Label htmlFor="name" md={4}>
                            Tn
                          </Label>
                          <Col md={8}>
                            <Control.text
                              model=".name"
                              id="name"
                              name="name"
                              placeholder="Tn nhn vin"
                              className="form-control"
                              validators={{
                                required,
                                minLength: minLength(3),
                                maxLength: maxLength(30),
                              }}
                            />
                            <Errors
                              className="text-danger"
                              model=".name"
                              show="touched"
                              messages={{
                                required: "Vui lng nhp!",
                                minLength: "Tn phi ln hn 2 k t",
                                maxLength: "Tn phi b hn 30 k t",
                              }}
                            />
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Label htmlFor="doB" md={4}>
                            Ngy sinh
                          </Label>
                          <Col md={8}>
                            <Control
                              model=".doB"
                              type="date"
                              id="doB"
                              name="doB"
                              className="form-control"
                              validators={{
                                required,
                              }}
                            />
                            <Errors
                              className="text-danger"
                              model=".doB"
                              show="touched"
                              messages={{
                                required: "Vui lng nhp!",
                              }}
                            />
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Label htmlFor="startDate" md={4}>
                            Ngy vo cng ty
                          </Label>
                          <Col md={8}>
                            <Control
                              model=".startDate"
                              type="date"
                              id="startDate"
                              name="startDate"
                              className="form-control"
                              validators={{
                                required,
                              }}
                            />
                            <Errors
                              className="text-danger"
                              model=".startDate"
                              show="touched"
                              messages={{
                                required: "Vui lng nhp!",
                              }}
                            />
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Label htmlFor="department" md={4}>
                            Phng ban
                          </Label>
                          <Col md={8}>
                            <Control.select
                              model=".department"
                              id="department"
                              name="department"
                              className="form-control"
                            >
                              <option>Sale</option>
                              <option>HR</option>
                              <option>Marketing</option>
                              <option>IT</option>
                              <option>Finance</option>
                            </Control.select>
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Label htmlFor="salaryScale" md={4}>
                            H s lng
                          </Label>
                          <Col md={8}>
                            <Control.text
                              model=".salaryScale"
                              id="salaryScale"
                              name="salaryScale"
                              placeholder="1.0 -> 4.0"
                              className="form-control"
                              validators={{
                                required,
                                isNumber,
                              }}
                            />
                            <Errors
                              className="text-danger"
                              model=".salaryScale"
                              show="touched"
                              messages={{
                                required: "Vui lng nhp!",
                                isNumber: "Vui lng nhp 1 s",
                              }}
                            />
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Label htmlFor="annualLeave" md={4}>
                            S ngy ngh cn li
                          </Label>
                          <Col md={8}>
                            <Control.text
                              model=".annualLeave"
                              id="annualLeave"
                              name="annualLeave"
                              className="form-control"
                              validators={{
                                required,
                                isNumber,
                              }}
                            />
                            <Errors
                              className="text-danger"
                              model=".annualLeave"
                              show="touched"
                              messages={{
                                required: "Vui lng nhp!",
                                isNumber: "Vui lng nhp 1 s",
                              }}
                            />
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Label htmlFor="overTime" md={4}>
                            S ngy  lm thm
                          </Label>
                          <Col md={8}>
                            <Control.text
                              model=".overTime"
                              id="overTime"
                              name="overTime"
                              className="form-control"
                              validators={{
                                required,
                                isNumber,
                              }}
                            />
                            <Errors
                              className="text-danger"
                              model=".overTime"
                              show="touched"
                              messages={{
                                required: "Vui lng nhp!",
                                isNumber: "Vui lng nhp 1 s",
                              }}
                            />
                          </Col>
                        </Row>
                        <Row className="form-group">
                          <Col md={{ size: 10, offset: 2 }}>
                            <Button
                              type="submit"
                              color="primary"
                              value="submit"
                            >
                              Thm
                            </Button>
                          </Col>
                        </Row>
                      </LocalForm>
                    </ModalBody>
                  </Modal>
                </div>
              </div>
            </div>

            <div className="col-12 col-md-6 mt-3">
              <Form onSubmit={this.handleSearch}>
                <FormGroup row>
                  <div className="col-8 col-md-8">
                    <Input
                      type="text"
                      className="input"
                      id="search"
                      name="search"
                      placeholder="Nhp tn nhn vin"
                      innerRef={(input) => (this.search = input)}
                    />
                  </div>
                  <div className="col-4 col-auto">
                    <Button md={2} type="submit" value="submit" color="primary">
                      Tm
                    </Button>
                  </div>
                </FormGroup>
              </Form>
            </div>
          </div>
          <hr />
          <div className="row">{danhsachnv}</div>
          <br></br>
        </div>
      );
  }

  componentDidMount() {
    this.setState({
      filterArray: this.props.staffs.staffs,
    });
  }
}

export default StaffList;



